/** @format */

.cart {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  transition: all 0.1s ease 0s;
  &__open {
    visibility: visible;
    opacity: 1;
    .cart__right {
      transform: translateX(0);
    }
  }
  &__right {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 0;
    height: 100%;
    width: 420px;
    background: #ffffff;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    transform: translateX(100%);
    transition: all 0.3s ease 0s;
    @media (max-width: 479.98px) {
      width: 100%;
    }
    h2 {
      font-size: 24px;
      line-height: 1.2;
      font-weight: 700;
      margin-bottom: 30px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}
